import './non-admin-user.scss'
import React from 'react';
import {
  Title,
  Text,
  Button
} from '@patternfly/react-core';
import LockIcon from '@patternfly/react-icons/dist/esm/icons/lock-icon';

const NonAdminUser = () => {

  const triggerRedirect = () => {
    window.location.href = "/iam/my-user-access";
  }

  return <div className="nonAdminDiv">
    <LockIcon className="lockIcon" />
    <Title headingLevel="h2" size="xl" className="pf-u-mt-lg">You do not have access to Authentication Factors</Title>
    <Text className="pf-u-mt-lg">Contact your organization administrator(s) for more information, or visit My User Access to learn more about your permissions.</Text>
    <Button variant="link" onClick={triggerRedirect} className="pf-u-mt-lg">Go to landing page</Button>
  </div>
}

export default NonAdminUser;
